p{margin:0}a{display:block;color:#575757;text-decoration:none}body{color:#575757}.noselect{user-select:none}.blog{display:flex}.blog_content{width:80%;display:flex;min-height:100vh;flex-direction:column}.icon{width:1em;height:1em;overflow:hidden;cursor:pointer;vertical-align:-0.15em}.icon:hover{fill:#333 !important}.alert-enter{opacity:0;transform:scale(0.9)}.alert-enter-active{opacity:1;transform:translateX(0);transition:opacity 300ms, transform 300ms}.alert-exit{opacity:1}.alert-exit-active{opacity:0;transform:scale(0.9);transition:opacity 300ms, transform 300ms}
