.blog_sidebar{width:20%;display:flex;min-height:100vh;align-items:center;box-sizing:border-box;flex-direction:column;justify-content:center;border-right:1px dashed #dadada}.blog_sidebar_avatar{width:80px;height:80px;cursor:pointer;border-radius:40px;position:relative}.blog_sidebar_avatar img{width:100%;height:100%}.blog_sidebar_avatar .border{left:0;top:0;width:100%;height:100%;border-radius:40px;position:absolute;border:1px dashed #000}.blog_sidebar_avatar:hover .border{animation:scaleAni 1.2s ease-in-out infinite both}.blog_sidebar_link{width:100%;display:flex;align-items:center;margin:20px 0 60px;justify-content:center}.blog_sidebar_link .icon{margin:0 10px}.blog_sidebar_rights{color:#a8a8a8;font-size:12px;text-align:center;line-height:1.5}@keyframes scaleAni{0%{opacity:1;transform:scale(1)}50%{opacity:0.5;transform:scale(1.2)}100%{opacity:1;transform:scale(1)}}
